.home-container {
	position: absolute;
	display: flex;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
}

.home-content {
	position: relative;

	width: 728px;
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: auto;
}

.restaurant-logo {
	display: flex;
	position: relative;
	width: 100%;
	height: 61px;
	align-items: center;
	margin-top: 110px;
	justify-content: center;
}

.restaurant-logo img {
	max-width: 100%;
	max-height: 100%;
	min-height: 92px;
	position: relative;
	display: flex;
}

.btn-items {
	display: flex;
	position: relative;
	flex: auto;
	min-height: 173px;
	margin-top: 131px;
	margin-bottom: 30px;
	text-decoration: none;
}

.btn-items .item:not(:first-child) {
	margin-left: 16px
}

.item {
	display: inline-block;
	text-decoration: none;
	width: 168px;
	height: 171px;
	border: 1px solid #1492E680;
	border-radius: 10px;
	text-align: center;
	cursor: pointer;
	color: #000;
	transition: .3s
}

.item img {
	position: relative;
	margin: 20px 38px;
}

.item-1 img {
	width: 81px;
	height: 92px;
}

.item-2 img {
	width: 81px;
	height: 92px;
}

.red-circle {
	position: absolute;
	width: 30px;
	height: 30px;
	background-color: #F44336;
	z-index: 1;
	border-radius: 50%;
	right: 0;
	top: -10px;
}

.item-3 img {
	position: relative;
	width: 69px;
	height: 75px;
	margin: 0 auto;
}

.item-3 .item-3-container {
	position: relative;
	margin: 35px auto 23px auto;
	width: 69px;
	height: 75px;
}

.item-4 img {
	width: 92px;
	height: 92px;
	right: 2px;
}

.item span {
	white-space: nowrap;
	font: normal normal 600 20px/27px Montserrat;
}

.item-1 span {
	font: normal normal 600 18px/24px Montserrat;
}

.home-footer {
	display: flex;
	position: relative;
	width: 100%;
	margin-bottom: 31px;
	flex-direction: row;
	align-items: center;
}

.home-logo {
	position: relative;
	left: 0;
	display: flex;
}

.home-logo img {
	width: 129px;
	height: 22px;
}

.home-logout-container {
	position: relative;
	font: normal normal 600 14px/25px Montserrat;
	display: flex;
	flex: auto;
	justify-content: flex-end;
	text-decoration: none;
}

.home-logout {
	cursor: pointer;
	color: rgb(20, 115, 230);
	position: relative;
}

.item[href]:hover {
	box-shadow: 0 0 13px inset #aaa;
}

.item[href]:active {
	box-shadow: 0 0 20px inset #333;
	transition: .1s
}
